.main {
    width: 100%;
}
.header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.info {
    display: flex;
    align-items: center;
}
.info p {
    margin-right: 40px;
    position: relative;
    top: -2px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #37395C;
}
.info p::first-letter {
    text-transform: capitalize;
}
.date {
    position: relative;
    top: -2px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #37395C;
}
.textContainer {
    margin-top: 30px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #37395C;
}
.textContainer p {
    margin-bottom: 15px;
}
.textContainer p::first-letter {
    text-transform: capitalize;
}

@media screen and (max-width: 468px) {
    .info {
        flex-direction: column;
        align-items: flex-start;
        gap: 8px;
    }
    .header {
        flex-direction: column-reverse;
        align-items: flex-start;
        gap: 8px;
    }
}