.mainContainer {
    width: 100%;
    z-index: auto
}
.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    display: flex;
    flex-direction: column;
}
.itemName {
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    color: #37395C;
}
.itemName::first-letter {
    text-transform: capitalize;
}

/* CONTENT */
.content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 50px;
    margin-top: 30px;
    margin-bottom: 56px;
}
.images, .info {
    width: 100%;
}
.images {
    display: flex;
    justify-content: space-between;
}
.slider {
    width: 110px;
    height: 456px;
}
.slider div {
    height: fit-content !important;
}
.smallVideoContainer {
    width: 109px !important;
    height: 103px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid transparent;
    transition-duration: .3s;
    cursor: pointer;
}
.smallVideoContainer:hover {
    border: 1px solid #4B5EA2;
}
.videoShield {
    z-index: 1;
    width: 100%;
    height: 100%;
    position: relative;
    transition-duration: .3s;
}
.smallVideoContainer iframe {
    width: 110px !important;
    height: 103px;
    position: absolute;
    top: 0;
    z-index: 0;
    border: 0;
}
.smallImage {
    width: 110px !important;
    height: 103px;
    border: 1px solid transparent;
    transition-duration: .3s;
    object-fit: cover;
    cursor: pointer;
    opacity: .7;
}
.smallImage:hover {
    border: 1px solid #4B5EA2;
}
.activeImg {
    opacity: 1;
    border: 1px solid #4B5EA2;
}
.currentImageBlock {
    width: calc(100% - 110px);
    height: 456px;
    margin-left: 25px;
}
.currentImage {
    width: 100%;
    /* height: 100%; */
    max-height: 456px;
    /* object-fit: cover; */
    
}
.currentVideo {
    width: 100%;
    height: 456px;
}

/* INFO */
.info {
    display: flex;
    flex-direction: column;
}
.ratingCodeBlock {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.ratingCodeBlock span {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #37395C;
    text-transform: uppercase;
}
.priceBlock {
    display: flex;
    align-items: flex-end;
    gap: 25px;
    margin-top: 21px;
}
.price {
    font-weight: 700;
    font-size: 24px;
    color: #4B5EA2;
    position: relative;
}
.inAction {
    text-decoration: line-through;
    font-size: 20px;
}
.discount {
    font-weight: 700;
    font-size: 24px;
    color: #E86589;
}
.buyBut {
    font-family: 'Montserrat' !important;
    /* min-width: 110px !important; */
    border-radius: 15px !important;
    background-color: #9DC8CF !important;
    border: 1px solid #9DC8CF !important;
    color: white !important;
    font-size: 14px !important;
    font-weight: 700 !important;
    height: 32px !important;
    padding: 4px 24px !important;
}
.buyBut:hover {
    background-color: #77b2bb !important;
    border: 1px solid #77b2bb !important;
}
.buyBut:disabled {
    background-color: gray !important;
    border: 1px solid transparent !important;
}
.likeBut {
    color: #4B5EA2 !important;
}
.unlikeBut {
    color: #D56886 !important;
}
.actionBlock {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 25px;
}
.delivery {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 35px;
}
.deliveryTitle {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #4B5EA2;
    margin-bottom: 32px;
}
.deliveryPoints {
    width: 100%;
    box-sizing: border-box;
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.deliveryPoint {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.deliveryPoint h4 {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #D56886;
}
.deliveryPoint p {
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #2B629C;
}
.modalButtons {
    width: 100%;
    display: flex;
    gap: 120px;
    margin-top: auto;
}
.modalButtons button {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    text-decoration: underline;
    color: #4B5EA2;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    font-family: 'Montserrat';
}

/* DESCRIPTION */
.descriptionBlock {
    width: 100%;
    background: #E86589;
    min-height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 120px;
}
.descriptionContainer {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 50px 0;
    padding-bottom: 70px;
    color: white;
}
.leftDescription {
    width: 55%;
    position: relative;
}
.descriptionContainer h4 {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
}
.leftDescription p {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    margin-top: 26px;
}
.rightDescription {
    width: 38%;
}
.infoContainer {
    display: flex;
    width: 100%;
    flex-direction: column;
    margin-top: 26px;
    gap: 23px;
}
.infoPoint {
    display: flex;
    justify-content: space-between;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
}
.descText {
    max-height: 220px;
    overflow: hidden;
    transition-duration: .3s;
}
.fullDescText {
    max-height: 1050px
}
.leftDescription button {
    background-color: transparent;
    border: none;
    outline: none;
    color: white;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    padding: 0;
    font-family: 'Montserrat';
    margin-top: 25px;
    text-transform: uppercase;
    cursor: pointer;
}
.leftDescription button:hover {
    text-decoration: underline;
}
.hidingBlock {
    background: rgb(232,101,137);
    background: linear-gradient(180deg, rgba(232,101,137,0) 0%, rgba(232,101,137,1) 100%);
    width: 100%;
    height: 80px;
    position: absolute;
    bottom: 30px;
}

.same {
    width: 100%;
}

.viewed {
    margin-top: 50px;
    width: 100%;
}

/* REVIEWS */
.comments {
    width: 100%;
    margin-top: 105px;
    margin-bottom: 131px;
}
.comments h4 {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #3B3F61;
}
.commentsHeader {
    display: flex;
    align-items: center;
}
.commentsHeader span {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #37395C;
    margin-left: 14px;
}
.reviews {
    margin-top: 45px;
    display: flex;
    flex-direction: column;
    gap: 45px;
}
.emptyReview {
    display: flex;
    align-items: center;
    color: #37395C;
}
.emptyDesc {
    color: #37395C;
}
.form {
    margin-top: 25px;
}
.sliderHorizontal {
    display: none;
}
.loadMoreButt {
    background-color: #E86589 !important;
    border-radius: 36px !important;
    color: white !important;
    font-family: 'Montserrat' !important;
    font-weight: 700 !important;
    margin: 0 auto;
    padding: 10px 38px !important;
    margin-bottom: 40px !important;
    width: fit-content;
    margin:  auto !important;
}
.loadMoreButt:hover {
    background-color: #d85a7c !important;
}

@media screen and (max-width: 1170px) {
    .deliveryPoints {
        align-items: flex-start;
        padding-left: 0;
    }
    .deliveryPoint {
        flex-direction: column;
        gap: 8px;
        align-items: flex-start;
    }
    .info {
        width: 50%;
    }
    .modalButtons {
        flex-direction: column;
        gap: 16px;
        align-items: flex-start;
        margin-top: 24px;
    }
    .modalButtons button {
        padding: 0;
    }
}

@media screen and (max-width: 862px) {
    .content {
        gap: 25px;
    }
    .images {
        width: 50%;
        flex-direction: column-reverse;
        justify-content: flex-end;
    }
    .info {
        width: 50%;
    }
    .currentImageBlock {
        width: 100%;
        height: 307px;
        margin-left: 0px;
    }
    .smallVideoContainer {
        width: 70px !important;
        height: 70px;
    }
    .smallVideoContainer iframe {
        width: 70px !important;
        height: 70px;
    }
    .smallImage {
        width: 70px !important;
        height: 70px;
    }
    .slider {
        width: 70px;
        height: 307px;
        display: none;
    }
    .currentVideo {
        height: 307px;
    }
    .buyBut {
        height: fit-content !important;
    }
    .comments {
        margin-top: 20px;
    }
    .sliderHorizontal {
        display: block;
        margin-top: 16px;
    }
}

@media screen and (max-width: 568px) {
    .currentVideo {
        height: 456px;
    }
    .currentImageBlock {
        height: 456px;
    }
    .images {
        width: 100%;
    }
    .info {
        width: 100%;
    }
    .content {
        flex-direction: column;
    }
    .modalButtons {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .descriptionBlock {
        margin-bottom: 55px;
    }
    .descriptionContainer {
        flex-direction: column-reverse;
    }
    .rightDescription {
        width: 100%;
        margin-bottom: 32px;
    }
    .leftDescription {
        width: 100%;
    }
    .comments {
        margin-bottom: 55px;
    }
}

@media screen and (max-width: 468px) {
    .currentImageBlock, .currentVideo {
        height: 350px;
    }
    .currentImage {
        max-height: 350px;
    }
    .form {
        margin-top: 55px;
    }
    .modalButtons button {
        font-size: 12px;
    }
}